.card--testimonial {
    margin-top: $measure--small4;
}

.card--testimonial__quote {
    @include standfirst();
    margin-top: 0;
    color: $color-gray10;
}

.card--testimonial__quote-marks {
    @include zetatype();
    @include font-family('heading');
    color: $color-pink90;
}

.card--testimonial__author {
    margin-top: $measure--small4;

    p {
        @include smalltype();
        font-weight: $font-weight-bold;
        color: $color-gray7;
        margin-top: 0;
    }
}

.card--testimonial__column {
    &:first-child {
        margin-right: $measure--small2;
    }
}

.card--testimonial--photo {
    @include media('>tablet') {
        display: flex;
    }

    .card--testimonial__author {
        display: flex;
    }

    .card--testimonial__author__photo {
        margin-left: $measure--basic;
    }

    .card--testimonial__photo {
        @include media('>tablet') {
            margin-bottom: 0;
        }
    }

    .card--testimonial__photo--large {
        margin-bottom: $measure--small4;
        max-width: $measure--huge1;
        display: none;

        @include media('>tablet') {
            display: block;
        }
    }

    .card--testimonial__photo--small {
        max-width: $measure--medium3;

        @include media('>tablet') {
            display: none;
        }
    }
}
