.card--pathway__block {
    @include media('>between') {
        display: flex;
    }
}

.card--pathway {
    @include anchor-reset;
    @include anchor-default-outline--dark;
    @include underline-transition-title(
        $color-gray7,
        $hover-color: $color-gray10,
        $underline-width: 0.15rem
    );
    @include card-shared-properties;

    z-index: 1;
    border: $border-width-hairline $border-style-solid $color-gray2;
    display: block;
    padding: 0 $measure--basic $measure--basic $measure--basic;
    margin-bottom: $measure--small2;
    background: $color-purewhite;

    @include media('>between') {
        margin: 0 $measure--small2 0 0;
    }

    &:hover {
        @include media('hover') {
            &:after {
                border: none;
            }
        }
    }

    &:after {
        border: $border-width-hairline $border-style-solid $color-gray2;
    }

    &:focus,
    &:active {
        outline: none;
    }

    &:visited {
        border-color: $color-gray2;
    }

    &:before {
        content: '';
        height: $measure--quarter;
        width: 100%;
        display: block;
        margin-bottom: $measure--basic;
    }

    &:last-child {
        margin-right: 0;
    }

    .card__title {
        padding-bottom: 0;
        margin-top: $measure--basic;
    }

    p {
        margin-top: $measure--basic;
    }
}

@mixin pathway-card-background($color) {
    /*
        Pathway card uses same background
        color and scale transition as 
        other cards (course, related) but
        the colours need to be overriden
        due to all alternatives below. This
        local mixin takes care of this.
    */

    @include background-color-and-scale($color, 1.05);

    /*
        Sets top stripe color
    */
    &:before {
        background: $color;
    }
}

.card--pathway--blue {
    @include pathway-card-background($color-blue20);
}

.card--pathway--purple {
    @include pathway-card-background($color-purple20);
}

.card--pathway--pink {
    @include pathway-card-background($color-pink20);
}

.card--pathway--green {
    @include pathway-card-background($color-green20);
}

.card--pathway--purple {
    @include pathway-card-background($color-purple20);
}

.card--pathway--pink {
    @include pathway-card-background($color-pink20);
}

.card--pathway--teal {
    @include pathway-card-background($color-teal20);
}

.card--pathway--yellow {
    @include pathway-card-background($color-yellow20);
}
